import 'babel-polyfill'
import ac from './ac.js'
import appcan_api from 'appcan-api'
// import VueRouter from '../router'
import Vue from 'vue'
import Router from 'vue-router'
// let VueRouter = new Router()
import VueI18n from 'vue-i18n'
import Locale from './locale.json'
import name from '../utils/namespace'

import Es6Promise from 'es6-promise'
require('es6-promise').polyfill()

//系统错误捕获
const errorHandler = (error, vm) => {
  let attri = {
    message: error.message,
    stack: error.stack,
    microAppEventName: '微应用程序错误'
  }
  try {
    appcan_api.setEvent({
      eventId: 'microAppProgramErrorEvent',
      attri: attri
    })
  }catch(e){}
  console.error(error);

}

ac.start = function (option) {
  Vue.config.productionTip = false
  Vue.prototype.$defConfig = option.config
  window.__$appConfig = option.config
  // Vue.prototype.$customConfig = {}
  Vue.prototype.$customConfig = undefined
  Vue.use(VueI18n)
  Vue.use(Router)
  // 错误日志收集
  Vue.config.errorHandler = errorHandler;
  Vue.prototype.$throw = (error) => errorHandler(error, this);

  let i18n = new VueI18n({
    locale: 'zh-cn',
    messages: Locale
  })
  let VueRouter = new Router({
    routes: option.routes
  })
  // VueRouter.addRoutes(option.routes)
  // vue bug
  // VueRouter.options.routes = VueRouter.options.routes.concat(option.routes)

  this.$router = VueRouter
  // this.$router = VueRouter

  let op = {
    el: option.el,
    i18n,
    router: VueRouter,
    render: option.render,
    components: option.components,
    // routes: option.routes,
    config: option.config
  }
  let apis = Object.assign({},appcan_api,ac );
  window[name.nameappcan] = apis;
  new Vue(op);

  function nowIsTab(path) {
    const list = option.config.tabBar.list
    for (let i in list) {
      if (list[i].pagePath === path) {
        return true
      }
    }
    return false
  }
  VueRouter.beforeEach((route, redirect, next) => {
    var element = document.getElementById('scrollView')
    if (redirect.path === '/') {
      if (element != null) {
        scrollTop = element.scrollTop
      }
    }
    VueRouter.options.routes.forEach(element => {
      if ((element.name && route.name && (element.name === route.name)) || element.path === route.path) {
        if (nowIsTab(route.path)) {
          Vue.prototype.$customConfig = undefined
          window.__$pageConfig = undefined
        } else {
          Vue.prototype.$customConfig = element.component.config
          window.__$pageConfig = element.component.config
        }
      }
    })
    document.title = 'ACUI - ' + route.name
    next()
  })

  VueRouter.afterEach(route => {
    var element = document.getElementById('scrollView')
    if (route.path === '/') {
      Vue.nextTick(() => {
        if (element != null) {
          element.scrollTop = scrollTop
        }
      })
    } else {
      if (element != null) {
        element.scrollTop = 0
      }
    }
  })
}

let scrollTop = 0

export default ac



