const ua = window.navigator.userAgent,
  IS_ANDROID = !!ua.match(/Android/i),
  IS_IOS = !!ua.match(/iPhone|iPad|iPod/i),
  IS_APP = !!ua.match(/appcan|ACMobi/i),
  STATUS_BAR_HEIGHT = 800, // api.getStatusbarHeight(),
  DEFAULT_HEADER_HEIGHT = 46 + STATUS_BAR_HEIGHT,
  WIN_HEIGHT = IS_APP && IS_IOS ? window.screen.height : window.innerHeight,
  WIN_WIDTH = window.innerWidth,
  DEFAULT_CONTENT_HEIGHT = WIN_HEIGHT - DEFAULT_HEADER_HEIGHT,
  KEYBOARD_HEIGHT = 220,
  IS_IOS11 = !!ua.match(/CPU iPhone OS 11/i)
export default {
  IS_APP: IS_APP,
  IS_ANDROID: IS_ANDROID,
  IS_IOS: IS_IOS,
  IS_IOS11: IS_IOS11,
  STATUS_BAR_HEIGHT: STATUS_BAR_HEIGHT,
  DEFAULT_HEADER_HEIGHT: DEFAULT_HEADER_HEIGHT,
  DEFAULT_CONTENT_HEIGHT: DEFAULT_CONTENT_HEIGHT,
  WIN_WIDTH: WIN_WIDTH,
  WIN_HEIGHT: WIN_HEIGHT,
  KEYBOARD_HEIGHT: 220
}



