  //发送网络请求
  request: function request(obj) {
    if (!verificationObj(obj)) return;
    if (!this.IS_APP) {
      if (!obj.method) {
        obj.method = 'GET'; //设置默认请求格式
      }
      (0, _axios.webRequest)(obj);
      return;
    }
    var id = acNetwork.create();
    var params = {
      url: detectionParams(obj.url),
      id: id,
      data: detectionParams(obj.data),
      method: detectionParams(obj.method) || 'GET',
      dataType: detectionParams(obj.dataType)
    };
    if (detectionParams(obj.header)) {
      params.header = obj.header;
    }
    obj.id = id;
    obj.abort = function (callback) {
      return callback(acNetwork.requestAbort(id));
    };
    acNetwork.request(params, function (error, data) {
      if (error) {
        try {
          var attri = {
            url: params.url,
            data: JSON.stringify(params.data),
            method: params.method,
            microAppEventName: '微应用网络错误'
          };
          attri = Object.assign(attri, data);
          acNative.setEvent({
            eventId: 'microAppNetworkErrorEvent',
            attri: attri
          });
        } catch (e) {}
      }
      if (error === 0 && protect(obj.success)) {
        obj.success(data);
      } else if (error === -1 && protect(obj.fail)) {
        obj.fail(data);
      } else if (protect(obj.complete)) {
        obj.complete(data);
      } else {
        obj.complete(data);
      }
    });
    return obj;
  },
