  downloadFile: function downloadFile(obj) {
    if (!verificationObj(obj)) return;
    try {
      var id = acNetwork.create();
      var params = {
        url: detectionParams(obj.url),
        id: id,
        header: detectionParams(obj.header)
      };
      obj.id = id;
      obj.onProgressUpdate = function (callback) {
        acNetwork.downloadOnProgressUpdate(id, function (error, data) {
          return callback(data);
        });
      };
      obj.abort = function (callback) {
        return callback(acNetwork.downloadAbort(id));
      };
      acNetwork.downloadFile(params, function (error, data) {
        if (error) {
          try {
            var attri = {
              url: params.url,
              data: '',
              method: 'GET',
              microAppEventName: '微应用网络错误'
            };
            attri = Object.assign(attri, data);
            acNative.setEvent({
              eventId: 'microAppNetworkErrorEvent',
              attri: attri
            });
          } catch (e) {}
        }
        if (error === 0 && protect(obj.success)) {
          obj.success(data);
        } else if (error === -1 && protect(obj.fail)) {
          obj.fail(data);
        } else if (protect(obj.complete)) {
          obj.complete(data);
        } else {
          obj.complete(data);
        }
      });
      return obj;
    } catch (error) {
      try {
        if (obj.complete) obj.complete();
        obj.success();
      } catch (error) {
        console.log(simulationErrCode);
      }
      console.log(errCode + '' + error);
    }
  },
